import React, { useState } from 'react';
import axios from 'axios';
import { Dialog } from 'primereact/dialog';
import Loader from './Loader/Loader';

const ImageDialog = ({ dialogVisible, setDialogVisible, onUpload }) => {
  const [isLoading, setIsLoading] = useState(false); //    

  //      
  const handleChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setIsLoading(true); //   
      uploadImage(file);
    }
  };

  //     ImgBB
  const uploadImage = (file) => {
    const formData = new FormData();
    formData.append('image', file);

    const apiKey = '8d79781c54e19eb95c3563d7f55ffc83';
    const imgbbUrl = `https://api.imgbb.com/1/upload?key=${apiKey}`;

    //  POST-  ImgBB
    axios
      .post(imgbbUrl, formData)
      .then((response) => {
        const uploadedImageUrl = response.data.data.url; //     
        onUpload(uploadedImageUrl); //  URL   
        setDialogVisible(false); //     
      })
      .catch((error) => {
        console.error('  :', error);
        alert('  ');
      })
      .finally(() => {
        setIsLoading(false); //   
      });
  };

  return (
    <Dialog
      header=" :"
      visible={dialogVisible}
      contentClassName="pt-3"
      position="bottom"
      className="w-full flex flex-col"
      onHide={() => setDialogVisible(false)}
      draggable={false}
      resizable={false}>
      {isLoading ? (
        <div className="flex justify-center items-center w-full h-full">
          <Loader imageLoader="true" />
        </div>
      ) : (
        <div className="flex flex-col items-center">
          <label htmlFor="file" className="cursor-pointer">
            <span className="rounded-md w-40 py-3 px-2 text-white bg-orange-600"> </span>
          </label>
          <input id="file" type="file" onChange={handleChange} className="hidden" />
        </div>
      )}
    </Dialog>
  );
};

export default ImageDialog;
